
.up-section {
  width: 200px;
  height: 150px;
  vertical-align: top;
  position: relative;
  overflow: hidden;
  -webkit-box-shadow: 0 0 1px 0;
          box-shadow: 0 0 1px 0;
}
.up-section .close-upimg {
  position: absolute;
  top: 6px;
  right: 8px;
  display: none;
  z-index: 10;
  cursor: pointer;
}
.up-section .up-span {
  display: block;
  width: 100%;
  height: 100%;
  visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9;
  background: rgba(0, 0, 0, .5);
}
.up-section:hover .close-upimg {
  display: block;
}
.up-section:hover .up-span {
  visibility: visible;
}
.loading {
  border: 1px solid #D1D1D1;
  background: url('http://image.houxiaopang.com/baseform/loading.gif') no-repeat center;
}
.up-opcity {
  opacity: 0;
}
.upimg-div .up-section {
  width: 160px;
  height: 120px;
  overflow: hidden;
}
.up-img {
  width: 100%;
}




